import "./styles.css";
import menu from "./menu";

import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import { CardActionArea } from "@mui/material";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`
  };
}

export default function BasicTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          <Tab label="TOP" {...a11yProps(0)} />
          <Tab label="JG" {...a11yProps(1)} />
          <Tab label="MID" {...a11yProps(2)} />
          <Tab label="ADC" {...a11yProps(3)} />
          <Tab label="SUP" {...a11yProps(4)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <Card sx={{ maxWidth: 345 }}>
          <CardActionArea>
            <CardMedia
              component="img"
              height="140"
              image="/14fmG4MxFy4U4uyaY_yO_F-M1M_cffSl5"
              alt="omuomu"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                オムライス
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {menus(0)}
              </Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      </TabPanel>
      <TabPanel value={value} index={1}>
        {menus(1)}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {menus(2)}
        {menus(3)}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {menus(3)}
      </TabPanel>
    </Box>
  );
}

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}
